      grid-row: 1;
      font-size: 6vw;
      margin: 4rem 0 3rem;
   }
   .contact img {
      grid-column: 2 / 3;
      grid-row: 5;
   }
   .contact p:nth-of-type(1) {
      grid-column: 2 / 3;
      grid-row: 2;
   }
   .contact p:nth-of-type(2) {
      grid-column: 2 / 3;
      grid-row: 3;
      max-width: 800px;
      margin: 0 auto;
   }
   .contactList {
      height: auto;
      grid-column: 2 / 3;
      grid-row: 4;
      margin: 2rem 10%;
   }
   .contactList li img {
      width: 124px;
   }
}

@media only screen and (min-width: 1200px) {
   .about h2 {
      margin: auto 35% auto 1rem;
   }
   .about h2:last-of-type {
      margin: 1rem 15% 2rem;
   }
   .about img {
      width: calc(100% - 30%);
      margin-left: 30%;
   }
   .skills {
      display: grid;
      grid-template: auto / 15% 1fr 1fr 15%;
   }
   .contact {
      display: grid;
      grid-template: auto / 15% 1fr 15%;
   }
   .projects {
      display: grid;
      grid-template: auto / 15% 1fr 1fr 1fr 15%;
   }
   .about h1 {
      margin: 4rem 15%;
   }
}

